.btn {
  display: inline-flex;
  padding: 10px 20px;

  color: $color-default-white;

  background-color: $color-neon-blue;
  border: none;
  cursor: pointer;

  transition: opacity $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}

.button {
  display: block;
  margin: 0;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #f2f5f7;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-align: center;

  border-radius: 3px;
}
