@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.button {
  display: block;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #f2f5f7;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-align: center;
  border-radius: 3px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  border-radius: 3px;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  background-color: #ffffff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
  }
}
.modal__close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  padding: 10px;
  border: 0;
  background-color: transparent;
}
.modal__close-button::after, .modal__close-button::before {
  content: "";
  height: 30px;
  width: 1px;
  background: #ffffff;
  border-radius: 5px;
  position: absolute;
  top: 5px;
  left: 20px;
  box-shadow: none;
}
.modal__close-button::after {
  transform: rotate(45deg);
}
.modal__close-button::before {
  transform: rotate(-45deg);
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  position: fixed;
  width: 100%;
  z-index: 100;
  display: flex;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f7;
}
.main-header__wrapper {
  display: grid;
  grid-template-columns: auto 1fr auto;
  padding: 28px 60px 37px 0;
  margin: 0 auto;
  max-width: 1220px;
  align-items: center;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    padding: 20px 20px 40px 0;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding: 10px 10px 20px 0;
    align-items: center;
  }
}
.main-header__logo-wrapper {
  display: flex;
}
@media (hover: hover) {
  .main-header__logo-wrapper:hover:not(.focus-visible) .main-header__logo-icon {
    opacity: 0.6;
  }
  .main-header__logo-wrapper:hover:not(.focus-visible) .main-header__logo-title {
    opacity: 0.6;
  }
}
.main-header__logo-icon {
  margin: auto 0;
  margin-right: 15px;
}
@media (max-width: 767px) {
  .main-header__logo-icon {
    width: 35px;
    height: 35px;
  }
}
.main-header__contacts-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .main-header__contacts-list {
    margin: 0;
    margin-left: auto;
  }
}
.main-header__contacts-item {
  margin: 5px 30px 5px 0;
  min-width: 224px;
}
.main-header__contacts-item:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .main-header__contacts-item {
    min-width: auto;
    margin-right: 20px;
  }
}
.main-header__contacts-icon {
  margin-right: 15px;
}
.main-header__contacts-text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .main-header__contacts-text {
    display: none;
  }
}
.main-header__logo-title {
  margin: auto 0;
}
@media (max-width: 767px) {
  .main-header__logo-title {
    display: none;
  }
}
.main-header__mail {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .main-header__mail {
    min-width: auto;
  }
}
.main-header__phone-link {
  display: flex;
  align-items: center;
}
@media (hover: hover) {
  .main-header__phone-link:hover:not(.focus-visible) .main-header__contacts-text {
    opacity: 0.6;
  }
  .main-header__phone-link:hover:not(.focus-visible) .main-header__contacts-icon {
    opacity: 0.6;
  }
}
.main-header__call-button {
  display: inline-block;
  padding: 10px 15px;
  margin: 0;
  box-sizing: border-box;
  border: 1px solid #f2f5f7;
}
@media (max-width: 1023px) {
  .main-header__call-button {
    display: none;
  }
}
@media (hover: hover) {
  .main-header__call-button:hover:not(.focus-visible) {
    background-color: #00b2ff;
    outline: none;
  }
}
.main-header__call-button.focus-visible:focus {
  background-color: #00b2ff;
  outline: none;
}

.main-footer {
  display: flex;
  flex-direction: column;
  background: #041827;
  color: rgba(255, 255, 255, 0.7);
}
.main-footer__social {
  display: flex;
  flex-direction: column;
  margin-right: auto;
}
@media (max-width: 1023px) {
  .main-footer__social {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    grid-column: span 2;
  }
}
@media (max-width: 767px) {
  .main-footer__social {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    padding: 23px 25px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}
.main-footer__logo-wrapper {
  display: flex;
  margin-right: 10px;
  margin-bottom: 25px;
}
@media (max-width: 1023px) {
  .main-footer__logo-wrapper {
    margin-bottom: 39px;
  }
}
@media (max-width: 767px) {
  .main-footer__logo-wrapper {
    margin-bottom: 10px;
  }
}
@media (hover: hover) {
  .main-footer__logo-wrapper:hover:not(.focus-visible) .main-footer__logo-icon {
    opacity: 0.6;
  }
  .main-footer__logo-wrapper:hover:not(.focus-visible) .main-footer__logo-title {
    opacity: 0.6;
  }
}
.main-footer__logo-icon {
  margin-right: 14px;
}
.main-footer__social-list {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .main-footer__social-list {
    margin-right: -15px;
  }
}
@media (max-width: 767px) {
  .main-footer__social-list {
    margin: 0 auto;
  }
}
.main-footer__social-link {
  display: block;
  width: 40px;
  height: 40px;
  text-align: center;
  padding: 5px;
}
@media (hover: hover) {
  .main-footer__social-link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-footer__social-link.focus-visible:focus {
  opacity: 0.6;
}
.main-footer__social-icon {
  width: 30px;
  height: 30px;
}
.main-footer__social-item:not(:last-child) {
  margin-right: 15px;
}
.main-footer__wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 55px 50px 55px 50px;
  margin: 0 auto;
  width: 100%;
  max-width: 1316px;
}
@media (max-width: 1023px) {
  .main-footer__wrapper {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    padding: 50px 50px 55px 50px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    display: flex;
    flex-direction: column;
    padding: 17px 0 35px 0;
    width: 100%;
  }
}
.main-footer__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 20px;
}
.main-footer__navigation {
  display: flex;
  flex-direction: column;
  margin-right: 30px;
}
@media (max-width: 1023px) {
  .main-footer__navigation {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .main-footer__navigation {
    position: relative;
    width: 100%;
    padding: 1px 25px;
    padding-top: 22px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}
.main-footer__navigation-list {
  display: grid;
  grid-template-columns: repeat(2, 218px);
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .main-footer__navigation-list {
    display: flex;
    flex-direction: column;
  }
}
.main-footer__navigation-item {
  min-width: 220px;
}
.main-footer__navigation-item:nth-of-type(5) {
  grid-column: 2;
  grid-row: 1;
}
.main-footer__navigation-item:nth-of-type(6) {
  grid-column: 2;
  grid-row: 2;
}
.main-footer__navigation-item:nth-of-type(7) {
  grid-column: 2;
  grid-row: 3;
}
@media (max-width: 1023px) {
  .main-footer__navigation-item {
    min-width: auto;
  }
}
.main-footer__contacts {
  width: 345px;
}
@media (max-width: 1023px) {
  .main-footer__contacts {
    width: 320px;
  }
}
@media (max-width: 767px) {
  .main-footer__contacts {
    position: relative;
    padding: 0 25px;
    padding-top: 22px;
    max-width: 100%;
    width: auto;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}
.main-footer__adress {
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
}
@media (max-width: 767px) {
  .main-footer__adress {
    font-size: 14px;
    line-height: 135%;
