.main-footer-list__link {
  color: rgba(255, 255, 255, 0.7);
}
@media (hover: hover) {
  .main-footer-list__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-footer-list__link.focus-visible:focus {
  opacity: 0.6;
}
.main-footer-list__text {
  font-size: 16px;
  line-height: 135%;
}
@media (max-width: 767px) {
  .main-footer-list__text {
    font-size: 14px;
    line-height: 25px;
  }
}

@media (max-width: 767px) {
  .accordion-item {
    position: relative;
  }
}
@media (max-width: 767px) and (max-width: 767px) {
  .accordion-item__body {
    visibility: hidden;
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.9s ease-in-out;
  }
}
@media (max-width: 767px) {
  .accordion-item.is-open .accordion-item__body {
    visibility: visible;
    height: auto;
    overflow: initial;
    opacity: 1;
  }
}
@media (max-width: 767px) {
  .accordion-item::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 25px;
    right: 27px;
    background-color: #f8fdff;
  }
}
@media (max-width: 767px) {
  .accordion-item::before {
    content: "";
    position: absolute;
    width: 1px;
    height: 15px;
    top: 18px;
    right: 34px;
    background-color: #f8fdff;
  }
}
@media (max-width: 767px) {
  .accordion-item.is-open::before {
    transform: rotate(90deg);
    transition: 0.9s;
  }
}
@media (max-width: 767px) {
  .accordion-item.no-js .accordion-item__body {
    visibility: visible;
    height: auto;
    overflow: initial;
    opacity: 1;
  }
}
@media (max-width: 767px) {
  .accordion-item.no-js::after {
    content: "";
    display: none;
  }
}
@media (max-width: 767px) {
  .accordion-item.no-js::before {
    display: none;
  }
}
